:root {
  /* text colors  */

  --dark-gray-main: rgb(32, 38, 40);
  --light-gray-main: rgb(63, 74, 78);

  /* component colors */

  --deep-pink: #e73a8eff;
  --vanilla: rgb(245, 240, 190);
  --hookers-green: rgb(192, 192, 192);
  --space-cadet: #131b45ff;
  --amaranth: rgb(198, 69, 110);
}
  
html {
  background-color: var(--space-cadet);
  height: 100%;
  width: 100%;
  background-image: url(assets/bliss-background.png);
  background-size: cover;
}

body {
  margin: 0;
  font-family: "IBM Plex Mono", monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  padding-bottom: 100px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.page-container {
  margin: 0;
  text-align: center;
  box-sizing: border-box;
  height: 100%;
  width: 100%;
}

.left-container {
  display: inline-block;
  position: absolute;
  right: 55%;
  top: 50px;
  min-width: fit-content;
  margin: 0;
  width: 20%;
  overflow: hidden;
  padding: 0.45rem;
  background-color: var(--hookers-green);
  outline: 5px solid gray;
  outline-style: outset;
  border: 2px solid gray;
  z-index: 1;
}

.right-container {
  display: inline-block;
  position: absolute;
  margin: 0;
  width: 20%;
  left: 55%;
  top: 50px;
  min-width: fit-content;
  padding: 0.45rem;
  align-items: center;
  background-color: var(--hookers-green);
  outline: 5px solid gray;
  outline-style: outset;
  border: 2px solid gray;
  z-index: 1;
}

.left-container-hidden {
  visibility: hidden;
}

.right-container-hidden {
  visibility: hidden;
}

.window-header {
  border: 1px solid gray;
  outline: 2px gray;
  outline-style: outset;
  background-color: var(--vanilla);
  cursor: grab;
  position: relative;
  right: 1rem;
  left: 2rem;
  opacity: 0.7;
  width: 89%;
  text-wrap: nowrap;
}

.xButton {
  position: absolute;
  left: 5px;
  top: 5px;
  background-color: var(--hookers-green);
  font-size: large;
}

.xButton:hover {
  background-color: var(--amaranth);
  color: var(--vanilla);
}

h1 {
  color: var(--space-cadet);
}

ul {
  list-style-type: none;
  padding-left: 0;
}

.gif {
  height: 44rem;
  margin: 1rem 1rem 1rem 2rem;
  border: 2px solid var(--dark-gray-main);
}

.link-button {
  width: 26rem;
  height: fit-content;
  margin: 2px;
  background-color: var(--deep-pink);
  cursor: pointer;
}

.link-button:hover {
  background-color: var(--amaranth);
}

svg {
  width: 3rem;
  height: 3rem;
  padding: 1rem;
  color: var(--vanilla);
}

footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 0;
  text-align: center;
  background-color: var(--hookers-green);
  opacity: 60%;
  font-size: 12px;
}

footer * {
  display: inline-block; /* Makes the child elements inline */
  vertical-align: middle;
}
@media (max-height: 920px){
  footer{ 
    display: none;
  }
}
  
@media (max-width: 1300px) {
  html{
    overflow: auto;
  }
  body{
    padding-top: 0;
  }
  footer{ 
    display: none;
  }
  }

@media (max-width: 1100px) {
  body{
    height: 1300px;
  }
  .link-button {
    width: 20rem;
    height: auto;
  }
  .gif {
    height: 100%;
  }
  .window-header.right {
    right: 4rem;
  }

  .right-container {
    top: 600px;
    left: 31%;
    width: 42%;
  }

  .left-container {
    top: 15px;
    left: 31%;
    width: 42%
  }
}

@media (max-width: 770px) {
  .right-container{
    left: 28%;
  }
  .left-container{
    left: 28%;
  }
}

@media (max-width: 570px) {

  .gif {
    height: 26rem;
    margin: 1rem 1rem 1rem 2rem;
    border: 2px solid var(--dark-gray-main);
  }
  
  .link-button {
    width: 100%;
    height: auto;
    margin: 2px;
    background-color: var(--deep-pink);
    cursor: pointer;
  }

  body {
    padding-top: 1rem;
    font-size: 14px;
    height: 1100px;
  }

  svg {
    height: 2rem;
    width: auto;
  }

  .right-container {
    top: 540px;
    left: 9%;
    width: 78%;
  }

  .left-container {
    top: 3%;
    left: 9%;
    width: 78%;
  }
}

@media (max-width: 370px) {

  body{
    font-size: 10px;
    height: 1000px;
  }

  .window-header{
    width: 83%
  }

  .gif {
    height: 19.5rem;
    margin: 1rem 1rem 1rem 2rem;
    border: 2px solid var(--dark-gray-main);
  }
  
  .link-button {
    width: 100%;
    height: auto;
    margin: 2px;
    background-color: var(--deep-pink);
    cursor: pointer;
  }

  .right-container {
    top: 430px;
    left: 9%;
    width: 78%
  }

  .left-container {
    top: 10ox;
    left: 9%;
    width: 78%;
  }

}

@media (max-width: 300px) {

  .gif {
    height: 18rem;
    margin: 1rem 1rem 1rem 2rem;
    border: 2px solid var(--dark-gray-main);
  }
  .right-container {
    top: 400px;
  }
  .link-button {
    width: 13.2rem;
    height: auto;
    margin: 2px;
    background-color: var(--deep-pink);
    cursor: pointer;
  }

}

